import { FC, ChangeEvent, useState } from 'react'
import PropTypes from 'prop-types'
import {
  Tooltip,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material'

import { Conference } from 'src/models/Conference'
import DeleteTwoToneIcon from '@mui/icons-material/DeleteTwoTone'
import BulkActions from './BulkActions'
import { Link, useParams } from 'react-router-dom'

interface ConferencesTableProps {
  className?: string
  conferences: Conference[]
  handleDelete(ids: number[]): void
}

const applyPagination = (conferences: Conference[], page: number, limit: number): Conference[] => {
  return conferences.slice(page * limit, page * limit + limit)
}

const ConferencesTable: FC<ConferencesTableProps> = ({ conferences, handleDelete }) => {
  const [selectedConferences, setSelectedConferences] = useState<number[]>([])
  const selectedBulkActions = selectedConferences.length > 0
  const [page, setPage] = useState<number>(0)
  const [limit, setLimit] = useState<number>(5)
  const params = useParams()
  const clubId = params.id

  const handleSelectAllConferences = (event: ChangeEvent<HTMLInputElement>): void => {
    setSelectedConferences(event.target.checked ? conferences && conferences.map(conference => conference.id) : [])
  }

  const handleSelectOneConference = (event: ChangeEvent<HTMLInputElement>, conferenceId: number): void => {
    if (!selectedConferences.includes(conferenceId)) {
      setSelectedConferences(prevSelected => [...prevSelected, conferenceId])
    } else {
      setSelectedConferences(prevSelected => prevSelected.filter(id => id !== conferenceId))
    }
  }

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage)
  }

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value))
  }

  const paginatedConferences = applyPagination(conferences, page, limit)
  const selectedSomeConferences = selectedConferences.length > 0 && selectedConferences.length < conferences.length
  const selectedAllConferences = selectedConferences.length === conferences.length
  const theme = useTheme()

  return (
    <Card>
      <Box height="55px">
        {selectedBulkActions ? (
          <Box flex={1} p={1}>
            <BulkActions selectedConferences={selectedConferences} handleDelete={handleDelete} />
          </Box>
        ) : (
          <CardHeader title="행사 목록" />
        )}
      </Box>

      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell padding="checkbox">
                <Checkbox
                  color="primary"
                  checked={selectedAllConferences}
                  indeterminate={selectedSomeConferences}
                  onChange={handleSelectAllConferences}
                />
              </TableCell>
              <TableCell>행사명</TableCell>
              <TableCell>학기</TableCell>
              <TableCell>날짜</TableCell>
              <TableCell>참석 인원</TableCell>
              <TableCell align="right" />
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedConferences.map(conference => {
              const isConferenceSelected = selectedConferences.includes(conference.id)
              return (
                <TableRow hover key={conference.id} selected={isConferenceSelected}>
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isConferenceSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneConference(event, conference.id)
                      }
                      value={isConferenceSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Link
                      to={`/manage/${clubId}/attendance/${conference.id}`}
                      state={conference}
                      style={{ textDecorationColor: 'black' }}
                    >
                      <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                        <Box style={{ display: 'flex', alignItems: 'baseline' }}>{conference.title}</Box>
                      </Typography>
                    </Link>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {conference.semester}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {conference.startAt}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {conference.member.length === 0
                        ? '-'
                        : `${conference.member[0].name} 외 ${conference.member.length - 1}명`}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="삭제" arrow onClick={() => handleDelete([conference.id])}>
                      <IconButton
                        sx={{
                          '&:hover': { background: theme.colors.error.lighter },
                          color: theme.palette.error.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <DeleteTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={conferences.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
    </Card>
  )
}

ConferencesTable.propTypes = {
  conferences: PropTypes.array.isRequired
}

ConferencesTable.defaultProps = {
  conferences: []
}

export default ConferencesTable
