import { Card } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Conference } from 'src/models/Conference'
import ConferencesTable from './ConferencesTable'

function ConferenceList() {
  const params = useParams()
  const [conferenceList, setConferenceList] = useState<Conference[]>([])
  const [stateForGetConferences, setStateForGetConferences] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()

  const handleDelete = async (ids: number[]): Promise<void> => {
    ids.forEach(async id => {
      try {
        const { data } = await axios.delete(`${process.env.REACT_APP_APIHOST}/club/conference?conferenceId=${id}`)

        if (data.message !== 'ok') {
          enqueueSnackbar('삭제 중 오류가 발생했습니다. 다시 시도해주십시오.', { variant: 'error' })
        }
      } catch (e) {
        enqueueSnackbar('삭제 중 오류가 발생했습니다. 다시 시도해주십시오.', { variant: 'error' })
      }
    })

    setStateForGetConferences(!stateForGetConferences)
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/conference?clubId=${params.id}`)
      .then((res: AxiosResponse<Conference[]>) => {
        setConferenceList(res.data)
      })
  }, [stateForGetConferences])

  return (
    <Card>
      <ConferencesTable conferences={conferenceList} handleDelete={handleDelete} />
    </Card>
  )
}

export default ConferenceList
